import moment from 'moment';

// FILTERS REDUCERS

const filtersReducerDefaultState = {
    text: '',
    sortBy: 'date', 
    startDate: moment().startOf('month'),
    endDate: moment().endOf('month')
}

export default (state = filtersReducerDefaultState, action) => {
    switch(action.type) {
        case "SET_TEXT_FILTER":
            // console.log("changing filter: ", state, action.text);
            return {                
                ...state,
                text: action.text
            };
            case "SORT_BY_AMOUNT":
                return {
                    ...state,
                    sortBy: 'amount'
            };
            case "SORT_BY_DATE":
                return {
                    ...state,
                    sortBy: 'date'
            };
            case "SET_START_DATE":
                // console.log("set filter starting date", action.startDate)
                return {
                    ...state,
                    startDate: action.startDate
            };
            case "SET_END_DATE":
                return {
                    ...state,
                    endDate: action.endDate
            };
        default:
            return state;
    }
    
}